import { useState, useEffect } from "react";
import "./App.css";
import "../node_modules/animate.css/animate.css";

function App() {
  const [modal, setModal] = useState(false);
  const [modalVideo, setModalVideo] = useState(false);

  useEffect(() => {
    setModal(true);
  }, []);

  return (
    <div className="App">
      <button className="openModalButton" onClick={() => setModal(!modal)}>
        Open PopUp
      </button>
      {modal ? (
        <div className="modal">
          <div className="cup"></div>
          <div className="cupbackground"></div>
          <div className="sparks1"></div>
          <div className="sparks2"></div>
          <div className="banner"></div>
          <div className="bannerBackground "></div>
          <button
            className="spotButton"
            onClick={() => setModalVideo(!modalVideo)}
          ></button>
          <span className="spotText">watch tv spot</span>
          <div className="bg1"></div>
          <div className="bg2"></div>
          <span className="text">ONE</span>
          <span className="text2">GAME</span>
          <span className="text3">TO GLORY</span>
          <div className="logo1"></div>
          <div className="logo2"></div>
          {modalVideo ? (
            <div className="modalBackground">
              <div className="backgroundVideo"></div>
              <button className="playVideoButton"></button>
              <button
                className="closeVideoButton"
                onClick={() => setModalVideo(!modalVideo)}
              ></button>
            </div>
          ) : null}
          <button
            className="closeButton"
            onClick={() => setModal(!modal)}
          ></button>
        </div>
      ) : null}
    </div>
  );
}

export default App;
